.container{
    max-height: calc(100vh - 60px);
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 2rem;
}

.pollingTable {
    margin-bottom: 1rem;
    border-radius: 10px;
    overflow: hidden;
}

.pollingTable * {
    padding: 0.3rem 0.7rem 0.3rem 0.7rem;
}

.pollingTable thead{
    background-color: #193248;
    color: #fff;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.pollingTable thead tr * {
    padding: 1rem;
    margin: 1rem;
    text-align: center;
}

.pollingTable tbody tr:nth-child(odd){
    background-color: #dddddd;
}

.pollingTable tbody tr:nth-child(even){
    background-color: #ebebeb;
}

.pollingTable tbody tr td input {
    border-radius: 3px;
    border: 1px solid #999999;
}

.pollingTable tbody tr *{
    color: #000;
    font-family: 'Open Sans', sans-serif;
}